import * as React from 'react';
import {
    Button,
    FormControl,
} from "@material-ui/core";
import {FormControlProps} from "@material-ui/core/FormControl";
import {CloudUpload} from "@material-ui/icons";
import InputFile, {InputFileComponent} from "../../../../components/InputFile";
import {MutableRefObject, RefAttributes, useImperativeHandle, useRef} from "react";

interface UploadFieldProps extends RefAttributes<any>{
    accept: string;
    label: string;
    setValue: (value) => void;
    error?: any;
    disabled?: boolean;
    FormControlProps?: FormControlProps;
}


export interface UploadFieldComponent{
    clear: () => void;
}

const UploadField =  React.forwardRef<UploadFieldComponent,UploadFieldProps>((props,ref) => {
    const fileRef = useRef() as MutableRefObject<InputFileComponent>
    const {accept,setValue, error, disabled, label} = props;

    useImperativeHandle(ref, () => ({
        clear: () => fileRef.current.clear()
    }));


    return (
        <FormControl
            error={error !== undefined}
            disabled={disabled === true}
            {...props.FormControlProps}
            fullWidth
            margin={"normal"}
        >
            <InputFile
                ref={fileRef}
                TextFieldProps={{
                    label: label,
                    InputLabelProps: {shrink: true},
                    style:{backgroundColor: '#ffffff'}
                }}
                InputFileProps={{
                    accept,
                    onChange(event){
                        const files = event.target.files as any;
                        files.length && setValue(files[0])
                    }
                }}
                ButtonFile={(
                    <Button
                        endIcon={<CloudUpload/>}
                        variant="contained"
                        color="primary"
                        onClick={() => fileRef.current.openWindow()}
                    >
                        Adicionar
                </Button>
                )}

            />
        </FormControl>
    );
});

export default UploadField;
